import request from '@/static/request.js'
export default {
  data() {
    return {
      userId: 0,
      id: 0,
      photo: '未填写',
      name: '未填写',
      wechat: '未填写',
      telephone: '未填写',
      birthday: '未填写',
      sex: '未填写',
      email: '未填写'
    }
  },
  methods: {
    onLoad(options) {
      uni.getStorage({
        key: 'StudentInfo',
        success: res => {
          this.id = res.data
        }
      });
      uni.getStorage({
        key: 'GetId',
        success: res => {
          let self = this;
          self.userId = res.data;
        }
      })
    }
  },
  mounted() {
    let vm = this;
    request
      .get('schoolmateData/', {
        schoolmate_id: vm.id,
      })
      .then(res => {
        vm.name = res.data[0].user_name
        if (res.data[0].user_gender === 'm') {
          vm.sex = '男'
        } 
        else {
          vm.sex = '女'
        }
        vm.telephone = res.data[0].user_telephone
        vm.email = res.data[0].user_email
        vm.city = res.data[0].user_city
      })
  }
};
